import React from 'react';
import { RenderToolbar, ItemSlot, SlotType } from '@ali/4ever-component';
import ToolbarButton from '../../components/toolbarButton';

export function createRenderToolbar(
  locale: Record<string, string>,
): RenderToolbar {
  return (props, controller) => {
    const {extraProps } = props;

    return  <ItemSlot display={SlotType.iconButton}>
      <ToolbarButton controller={controller} locale={locale} {...extraProps} />
    </ItemSlot>
  };
}
